<template>
    <section class="main-header">
        <div class="container flex justify-between align-center">
            <router-link to="/" class="nav-brand">
                <img :src="logo" alt="">
            </router-link>
            <nav class="main-nav">
                <router-link to="/about" class="nav-link">About me</router-link>
                <a href="https://github.com/acioliwilson/" target="_blank" class="nav-link">Latest Jobs</a>
                <a href="mailto:w.aciolib@gmail.com" class="btn btn-outline btn-outline-primary">Say hi!</a>
            </nav>
            <button class="toggle-btn" @click="toggleBtn" :class="{ active: isActive }">
                <i class="fa-solid fa-bars"></i>
            </button>
            <transition name="slide-fade">
                <nav class="mobile-nav" v-show="isToggled">
                    <div class="wrapper">
                        <router-link to="/" class="nav-link"><i class="fa-solid fa-house"></i></router-link>
                        <router-link to="/about" class="nav-link"><i class="fa-solid fa-glasses"></i></router-link>
                        <a href="https://github.com/acioliwilson" target="_blank" class="nav-link"><i class="fa-solid fa-newspaper"></i></a>
                        <a href="mailto:w.aciolib@gmail.com" class="nav-link"><i class="fa-solid fa-envelope"></i></a>
                    </div>
                </nav>
            </transition>
        </div>
    </section>
</template>

<script>
export default {
    name: 'MainHeader',
    data() {
        return {
            logo: 'logo.png',
            isToggled: false,
            isActive: false
        }
    },
    methods: {
        async toggleBtn () {
            this.isToggled = !this.isToggled
            this.isActive = !this.isActive
        }
    }
}
</script>

<style scope>
.main-header {
    width: 100%;
    display: flex;
    justify-content: center;
}
.container {
    width: 90%;
    max-width: 1728px;
    padding: 1rem 0;
    position: relative;
}
.flex { display: flex; }
.justify-between { justify-content: space-between; }
.align-center { align-items: center; }
.main-nav {
    display: flex;
    align-items: center;
}
.nav-link {
    color: #000;
    font-weight: 500;
    margin: 0 2rem 0 0;
    font-size: 16px;
}
.btn {
    padding: 10px 25px;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 500;
    transition: .3s ease all;
}
.btn-outline { border: 1px solid transparent; }
.btn-outline-primary { border-color: var(--primary-color); color: var(--primary-color); }
.btn-outline-primary:hover { background: var(--primary-color); color: #FFF; box-shadow: 0 5px 10px rgba(255, 51, 0, .3); }
.mobile-nav, .toggle-btn {
    display: none;
}

@media (max-width: 1040px) {
    .main-nav {
        display: none;
    }
    .mobile-nav {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-start;
        background: rgba(5, 4, 27, 0.4);
        backdrop-filter: blur(5px);
        --webkit-backdrop-filter: blur(5px);
        width: 100%;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        padding-top: 5rem;
    }
    .slide-fade-enter-active {
        transition: all 0.3s ease-out;
    }
    .slide-fade-leave-active {
        transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
    }
    .slide-fade-enter-from,
    .slide-fade-leave-to {
        transform: translateY(-1rem);
        opacity: 0;
    }

    .wrapper {
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin: 0 auto;
    }

    .nav-link {
        color: #000;
        background: #FFF;
        box-shadow: 5px 0 10px rgba(0,0,0,.2);
        border-radius: 20px;
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 1rem 0;
        transition: .3s ease all;
        animation: .3s fadeDown forwards;
        transform: translateY(-1rem);
        opacity: 0;
    }
    @keyframes fadeDown {
        from { transform: translateY(-1rem); opacity: 0; }
        to { transform: translateY(0); opacity: 1; }
    }
    .nav-link:hover {
        color: #FFF;
        background: var(--primary-color);
        box-shadow: 5px 0 10px rgba(255, 51, 0, .3);
    }
    nav a.router-link-exact-active {
        background: #FFF;
        border-radius: 20px;
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .3s ease all;
    }
    .nav-link:nth-child(1) {
        animation-delay: .3s;
    }
    .nav-link:nth-child(2) {
        animation-delay: .4s;
    }
    .nav-link:nth-child(3) {
        animation-delay: .5s;
    }
    .nav-link:nth-child(4) {
        animation-delay: .6s;
    }
    .toggle-btn {
        background: none;
        border: none;
        font-size: 25px;
        color: var(--primary-color);
        cursor: pointer;
        position: absolute;
        z-index: 3000;
        right: 0;
        transition: .3s ease all;
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 15px;
    }
    button.active {
        transform: rotate(360deg);
        background: #FFF;
        box-shadow: 5px 0 10px rgba(0,0,0,.2);
    }
}
</style>